@font-face {
  src: url(./Fonts/product-sans/Product\ Sans\ Bold.ttf);
  font-family: ProductSansBold;
}
@font-face {
  src: url(./Fonts/Montserrat-Bold.ttf);
  font-family: MontserratBold;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #f0f4f3;
  overflow: hidden;
}

#scrap {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2.7vh;
}

.loginMenu {
  margin-top: 12vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loginBox {
  display: flex;
  flex-direction: column;
  width: 428px;
  height: 368px;
  margin-top: 17px;
  background: #ffffff;
  box-shadow: 0px 0px 50px -16px rgba(0, 0, 0, 0.25);
  border-radius: 15px;
}
.logoBorder {
  border-right: 1.6px solid #12465b;
  width: 45px;
  height: 43px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loginInputs {
  height: 266px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.inputs {
  display: flex;
  background: #ffffff;
  border: 1.6px solid #12465b;
  width: 345px;
  height: 45px;
}
.input {
  display: inline;
  border: none;
  width: 300px;
}
.signBtn {
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  background: #d8574a;
  border-radius: 50px;
  height: 43px;
  width: 320px;
}
.buletM {
  border-radius: 50%;
  position: absolute;
  width: 89px;
  height: 89px;
  top: 23vh;
  left: 59.5vw;
  z-index: -999;
  background: #d8574a;
}
.buletK {
  position: absolute;
  width: 150px;
  height: 295px;
  border-radius: 50%;
  left: 90vw;
  top: 74vh;
  z-index: -999;
  background: #f7be38;
  border-radius: 150px 0 0 150px;
}
.logoIPB {
  position: absolute;
  top: 35vh;
  height: 70vh;
  z-index: -999;
}
h1 {
  font-family: MontserratBold;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 37px;
  color: #12465b;
}
h4 {
  font-family: ProductSansBold;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  color: #12465b;
}

@media screen and (max-width: 1200px) {
  .buletM {
    left: 69vw;
  }
}
@media screen and (max-width: 600px) {
  .buletM {
    top: 23vh;
    left: 79vw;
  }
  .buletK {
    top: 80vh;
    left: 80vw;
  }
}
